<template>
  <router-link
    id="holder"
    tag="div"
    :to="{ name: 'CertainSubCategory', query: { 'cat-name': category, 'subcat-name': subCategory.name } }"
  >
    <b-card :title="subCategory.name"
      :class="{ shadow }" @mouseenter="shadow = true" @mouseleave="shadow = false"
      :img-src="imgSrc" img-width="150px" img-height="150px" img-alt="product.name" img-top>
      <!-- <b-card-text class="mx-auto" id="titleId">{{ subCategory.name }} </b-card-text> -->
    </b-card>
  </router-link>
</template>

<script>
export default {
  name: 'SubCategoryBox',
  data () {
    return {
      shadow: false
    }
  },
  props: [
    'category',
    'subCategory'
  ],
  computed: {
    imgSrc () {
      return this.subCategory.image ? this.subCategory.image.link : ''
    }
  }
}
</script>

