<template>
  <b-container :fluid="true" class="main">
    <b-row>
      <b-col v-for="category in categories" :key="category.name" sm="4" cols="6" lg="2" md="3" class="my-3">
        <category-box :category="category"></category-box>
      </b-col>
    </b-row>
    <b-container fluid class="d-flex justify-content-center">
      <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block" @hidden="onHidden">
        <b-button :disabled="busy" variant="success" v-if="!categoryLoadingDone" @click="loadCategories">
          Load More
        </b-button>
      </b-overlay>
    </b-container>
  </b-container>
</template>

<script>
import CategoryBox from '../../components/Boxes/CategoryBox'
import loader from '../../mixins/loader'

export default {
  name: 'AllCategories',
  data () {
    return {
      categories: [],
      categoryLoadingDone: false,
      busy: false
    }
  },
  components: {
    CategoryBox
  },
  mixins: [
    loader
  ],
  methods: {
    onHidden () {
      this.$refs.button.focus()
    },
    loadCategories () {
      this.busy = true
      const offset = this.categories.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/categories/all', { offset, limit }, 'categories')
        .then(response => {
          this.busy = false
          this.categories = this.categories.concat(response.data.categories)
          if (response.data.categories.length < limit) {
            this.categoryLoadingDone = true
          }
        })
    }
  },
  created () {
    this.loadCategories()
  }
}
</script>

<style scoped>
.main {
  background-color: rgb(221, 221, 221);
}
</style>
