<template>
  <b-overlay :show="busy" opacity="0.4" spinner-small
                      spinner-variant="primary" class="d-inline block" @hidden="onHidden">
    <router-link
      id="holder"
      tag="div"
      :to="{ name: 'CertainCategory', query: { 'cat-name': category.name } }"
    >
      <b-card>
        <b-card-img no-body
          :src="imgSrc"
          :alt="category.name"
          top
          id="imageId"></b-card-img>
        <b-card-text class="mx-auto" id="titleId">{{ category.name }} </b-card-text>
      </b-card>
    </router-link>
  </b-overlay>
</template>

<script>
export default {
  name: 'CategoryBox',
  data () {
    return {
      busy: false
    }
  },
  props: [
    'category'
  ],
  computed: {
    imgSrc () {
      return this.category.image ? this.category.image.link : ''
    }
  },
  methods: {
    onHidden () {
      this.$refs.button.focus()
    }
  }
}
</script>

<style scoped>
#holder {
  cursor: pointer;
  padding: 0;
  text-align: center;
}
#titleId {
  font-size-adjust: inherit;
}
#imageId {
  height: auto;
  width: 90%;
  max-width: 250px;
  max-height: 250px;
}
</style>
