<template>
    <div class="bg-white">
        <div class="d-xs-block d-md-none">
            <div class="shadow mb-2">
                <b-dropdown id="dropdown-diviser" class="col-6" variant="Light" :text="sortOptions[sortId].text">
                    <b-dropdown-item-button
                      v-for="(option, index) in sortOptions"
                      :key="index"
                      @click="setSort(index)"
                    >
                      {{option.text}}
                    </b-dropdown-item-button>
                </b-dropdown>
                <b-button class="col-6" variant="Light" v-b-toggle.sidebar-footer>
                    <b-icon icon="funnel"></b-icon> Filter</b-button>
            </div>
            <b-sidebar id="sidebar-footer" no-close-on-route-change aria-label="Sidebar with custom footer" shadow width=70%>
                <template v-slot:footer>
                  <div class="mx-auto text-center">
                      <b-button squared variant="warning" @click="handleReset" class="col-6">Reset</b-button>
                      <b-button squared variant="success" @click="handleApplyFilter" class="col-6">Apply {{isFiltered}}</b-button>
                  </div>
                </template>
                <div class="px-3 py-2">
                    <b-form-group label="Brand">
                        <b-form-checkbox-group v-model="brand" :options="brands">

                        </b-form-checkbox-group>
                    </b-form-group>
                  <p>{{brand}}</p>
                    <span
                      v-for="(values, key, index) in specifications"
                      :key="index">
                      <b-form-group :label="key">
                        <b-form-checkbox-group v-model="filters[key]" :options="values">

                        </b-form-checkbox-group>
                      </b-form-group>
                      <p>{{filters[key]}}</p>
                    </span>
                    <b-form-group label="Rating">
                        <b-form-checkbox v-for="i in 5" v-model="rating" :value="i" :key="i">
                          <b-form-rating id="rat" inline no-border readonly variant="warning" :value="i">
                          </b-form-rating>
                          <small v-if="i<5">&nbsp;&nbsp;and up</small>
                        </b-form-checkbox>
                    </b-form-group>
                    <b-form inline>
                      <b-form-input placeholder="Min"
                        type="number"
                        v-model="min"
                        style="width:40%" >
                      </b-form-input>
                      <p class="mx-2 my-auto">-</p>
                      <b-form-input placeholder="Max"
                        type="number"
                        v-model="max"
                        style="width:40%"></b-form-input>
                    </b-form>
                    <hr>
                </div>
            </b-sidebar>
        </div>
        <div class="d-none d-md-block border-right">
            <div class="shadow mb-2">
              <b-dropdown id="dropdown-diviser" class="col-6" variant="Light" :text="sortOptions[sortId].text">
                <b-dropdown-item-button
                  v-for="(option, index) in sortOptions"
                  :key="index"
                  @click="setSort(index)"
                 >
                  {{option.text}}
                </b-dropdown-item-button>
              </b-dropdown>
            </div>
            <b-form-group label="Brand" class="ml-2">
                <b-form-checkbox-group v-model="brand" :options="brands">

                </b-form-checkbox-group>
            </b-form-group>
            <span
                v-for="(values, key, index) in specifications"
                :key="index">
                <b-form-group :label="key" class="ml-2 mt-2">
                    <b-form-checkbox-group v-model="filters[key]" :options="values" stacked>

                    </b-form-checkbox-group>
                </b-form-group>
                <hr>
                <!-- <p>{{filters[key]}}</p> -->
            </span>
            <b-form-group label="Rating" class="ml">
              <b-form-checkbox v-for="i in 5" v-model="rating" :value="i" :key="i">
                  <b-form-rating size="sm" id="rat" inline no-border readonly variant="warning" :value="6-i">
                  </b-form-rating>
                  <small v-if="i>1">andup</small>
              </b-form-checkbox>
            </b-form-group>
            <b-form class="" inline>
                <b-form-input placeholder="Min"
                  type="number"
                  v-model="min"
                  style="width:40%" >
                </b-form-input>
                <p class="mx-2 my-auto">-</p>
                <b-form-input placeholder="Max"
                  type="number"
                  v-model="max"
                  style="width:40%">
                </b-form-input>
            </b-form>
            <div class="mt-2">
                <b-button squared variant="warning" @click="handleReset" class="col-6">Reset</b-button>
                <b-button squared variant="success" @click="handleApplyFilter" class="col-6">Apply</b-button>
            </div>
        </div>
      <!-- <pre>{{specifications}}</pre>
      <pre>{{filters}}</pre>
      <pre>{{brands}}</pre>
      <pre>{{brand}}</pre>
      <pre>{{min + ' - ' + max}}</pre>
      <pre>{{rating}}</pre> -->
    </div>
</template>
<script>
import loader from '../mixins/loader'

export default {
  name: 'SideFilter',
  props: [
    'routeName',
    'properties',
    'brands',
    'initQuery'
  ],
  data () {
    return {
      min: null,
      max: null,
      rating: null,
      sortId: 0,
      sortOptions: [
        {
          text: 'Random Order',
          sort: null,
          order: null
        },
        {
          text: 'Price Low to High',
          sort: 'price',
          order: 'asc'
        },
        {
          text: 'Price High to Low',
          sort: 'price',
          order: 'desc'
        },
        {
          text: 'Rating',
          sort: 'rating',
          order: 'desc'
        }
      ],
      brand: [],
      filters: {}
    }
  },
  computed: {
    isFiltered () {
      let filtered = false
      filtered = filtered || Boolean(this.sortOptions[this.sortId].sort)
      filtered = filtered || Boolean(this.sortOptions[this.sortId].order)
      filtered = filtered || Boolean(this.rating)
      filtered = filtered || Boolean(this.min)
      filtered = filtered || Boolean(this.max)
      filtered = filtered || Boolean(Array.isArray(this.brand) && this.brand.length)
      for (const key in this.filters) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.filters.hasOwnProperty(key)) {
          filtered = filtered || Boolean(Array.isArray(this.filters[key]) && this.filters[key].length)
        }
      }
      return filtered
    },
    specifications () {
      const specs = {}
      for (const prop of this.properties) {
        if (prop.valueType === 'Fixed') {
          specs[prop.name] = prop.values
        }
      }
      return specs
    },
    query () {
      if (this.isFiltered) {
        return {
          'cat-name': this.categoryName,
          'subcat-name': this.subCategoryName,
          filtered: true,
          specifications: JSON.stringify(this.filters),
          brand: this.brand,
          pricemin: this.min,
          pricemax: this.max,
          rating: this.rating,
          sort: this.sortOptions[this.sortId].sort,
          order: this.sortOptions[this.sortId].order
        }
      } else {
        return {
          'cat-name': this.categoryName,
          'subcat-name': this.subCategoryName
        }
      }
    },
    categoryName () {
      return this.initQuery['cat-name']
    },
    subCategoryName () {
      return this.initQuery['subcat-name']
    }
  },
  mixins: [
    loader
  ],
  methods: {
    setSort (id) {
      if (this.sortId !== id) {
        this.sortId = id
        this.handleApplyFilter()
      }
    },
    loadFilters () {
      const sort = this.initQuery.sort
      const order = this.initQuery.order
      this.sortId = Math.max(this.sortOptions.findIndex(option => option.sort === sort && option.order === order), 0)
      this.brand = this.initQuery.brand
      if (this.brand && !Array.isArray(this.brand)) {
        this.brand = [this.brand]
      }
      this.min = this.initQuery.pricemin
      this.max = this.initQuery.pricemax
      this.rating = this.initQuery.rating
      for (const key in this.initQuery.specifications) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.initQuery.specifications.hasOwnProperty(key)) {
          this.filters[key] = this.initQuery.specifications[key]
        }
      }
    },
    handleApplyFilter () {
      this.$router.push({
        name: this.routeName,
        query: this.query
      }).catch(() => {})
    },
    handleReset () {
      this.loadFilters()
    }
  },
  created () {
    this.loadFilters()
  }
}
</script>
