<template>
  <b-container :fluid="true" id="main">
    <!-- <pre>{{$route.query}}</pre> -->
    <b-row class="d-block d-md-none">
      <side-filter
        :route-name="$route.name"
        :brands="brands"
        :properties="spec.properties"
        :init-query="$route.query"
      ></side-filter>
    </b-row>
    <!-- <b-card class="mt-3" header="Sub Category">
      <pre class="m-0">{{subCategory}}</pre>
    </b-card> -->
    <b-row>
      <b-col md="2" class="d-none d-md-block">
        <side-filter
          :route-name="$route.name"
          :brands="brands"
          :properties="spec.properties"
          :init-query="$route.query"
        ></side-filter>
      </b-col>
      <b-col>
        <b-row class="mt-2 d-none d-md-block">
          <div class="d-flex">
            <h3 class="">{{this.$route.query['subcat-name']}}</h3>
            <small class="my-auto ml-auto">Sort By: </small>
            <b-dropdown id="dropdown-diviser" class="ml-2 my-auto" variant="outline-primary"
                :text=dropText style="width:200px">
                <b-dropdown-item-button @click="dropText='Price Low to High'">Price Low to High</b-dropdown-item-button>
                <b-dropdown-divider></b-dropdown-divider>
                <b-dropdown-item-button @click="dropText='Price High to Low'">Price High to Low</b-dropdown-item-button>
            </b-dropdown>
          </div>
          <hr>
        </b-row>
        <b-row>
          <b-col  v-for="product in products" :key="product.id" class="my-3" sm="4" cols="6" lg="2" md="3">
            <product-box :product="product"></product-box>
          </b-col>
        </b-row>
        <b-container fluid class="d-flex justify-content-center">
          <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
            <b-button :disabled="busy" variant="success" v-if="!productLoadingDone" @click="loadProducts">
              Load More
            </b-button>
          </b-overlay>
        </b-container>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import ProductBox from '../../components/Boxes/ProductBox'
import SideFilter from '../../components/SideFilter'
import loader from '../../mixins/loader'

export default {
  name: 'CertainSubCategory',
  data () {
    return {
      subCategory: {},
      spec: {
        properties: []
      },
      brands: [],
      products: [],
      productLoadingDone: true,
      dropText: 'popyularity',
      busy: false
    }
  },
  components: {
    ProductBox,
    SideFilter
  },
  mixins: [
    loader
  ],
  methods: {
    loadSubCategory () {
      const categoryName = this.$route.query['cat-name']
      const subCategoryName = this.$route.query['subcat-name']
      this.loader('/sub-category',
        { 'cat-name': categoryName, 'subcat-name': subCategoryName },
        `${subCategoryName} under ${categoryName}`)
        .then(response => {
          this.subCategory = response.data.subcat
          this.spec = response.data.spec
          this.brands = response.data.brands
        })
    },
    setProducts (products, limit) {
      this.products = this.products.concat(products)
      if (products.length < limit) {
        this.productLoadingDone = true
        // this.busy = true
      } else {
        this.productLoadingDone = false
      }
    },
    loadProducts () {
      const categoryName = this.$route.query['cat-name']
      const subCategoryName = this.$route.query['subcat-name']
      if (!this.productLoadingDone) this.busy = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT

      if (this.$route.query.filtered) {
        const params = Object.assign({ offset, limit }, this.$route.query)
        delete params.filtered
        this.loader('/sort-and-filter', params,
        `filtered products of ${subCategoryName} under ${categoryName}`)
          .then(response => {
            this.busy = false
            this.setProducts(response.data.products, limit)
          })
      } else {
        this.loader('/products/sub-category',
          { 'cat-name': categoryName, 'subcat-name': subCategoryName, offset, limit },
          `products of ${subCategoryName} under ${categoryName}`)
          .then(response => {
            this.busy = false
            this.setProducts(response.data.products, limit)
          })
      }
    },
    clearProducts () {
      this.products = []
      this.productLoadingDone = false
    }
  },
  watch: {
    '$route' (to, from) {
      if (to.query.specifications) {
        to.query.specifications = JSON.parse(to.query.specifications)
      }
      if (to.query['cat-name'] !== from.query['cat-name'] || to.query['subcat-name'] !== from.query['subcat-name']) {
        this.loadSubCategory()
      }
      this.clearProducts()
      this.loadProducts()
    }
  },
  created () {
    if (this.$route.query.specifications) {
      this.$route.query.specifications = JSON.parse(this.$route.query.specifications)
    }
    this.loadSubCategory()
    this.loadProducts()
  }
}
</script>

<style scoped>
  #main {
    background-color: rgb(221, 221, 221);
  }
</style>
